.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60%;
  background-color: #0a081f;
  background-image: url(../assets/backGroundImage6.png);
  /* background-color: aqua; */
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* ****date section**** */
.date-main {
  height: 100%;
  width: 15%;
  color: #ffffff;
  position: relative;
  /* border: 1px solid black; */
  display: grid;
  place-items: center;
  align-content: space-between;
}
.device-location {
  font-size: 25px;
  width: 100%;
  cursor: pointer;
}

.deviceid,
.location {
  display: flex;
  justify-content: center;
}

.time {
  font-size: 70px;
}
.date {
  font-size: 25px;
}

/* **** Popup Section **** */
.popup {
  /* border: 1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -94px;
  left: 0;
  background-color: #ffffff;
  border-radius: 15px;
  padding: 8px;
  font-size: 12px;
  font-weight: 500;
  z-index: 11111111;
  height: 66px;
  width: 118px;
}
.levelcontainer,
.Volumecontainer,
.weightcontainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 2px;
}

label {
  display: flex;
  justify-content: space-between;
  width: 50px;
}
input {
  outline: none;
  border: none;
  width: 50px;
  background-color: #ffffff;
  font-size: 12px;
  font-weight: 700;
  margin-left: 10px;
}

/* ****dustbin section**** */

.dustbin-main {
