/* **** piechart-main ****  */

.pieChart-main {
  height: 100%;
  width: 30%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* border: 1px solid black; */
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}
.chartdata {
  width: 46%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
.line1,
.line2,
.line3 {
  width: 100%;
  display: flex;
  font-size: 14;
}
.line3 {
  text-align: left;
}
.line1::before,
.line2::before,
.line3::before {
  content: "";
  height: 15px;
  width: 15px;
  border-radius: 50%;
  margin: 5px;
}
.line1::before {
  background-color: #d9d9d9;
}
.line2::before {
  background-color: #072145;
}
.line3::before {
  background-color: #6296b2;
}

.piechart {
  margin-right: 10px;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  background: conic-gradient(
    #d9d9d9 0deg 80deg,
    #6296b2 80deg 190deg,
    #072145 190deg 360deg
  );
  display: flex;
  align-items: center;
  justify-content: center;
}

.piechart::before {
  content: "";
  content: "";
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #26767f;
}

@keyframes flash {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}

.flash {
  animation: flash 3s;
}
