.loadingContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh; /* Set height to fit the screen */
  width: 100%;
  /* background-image: url(../assets/backGroundImage.png); */
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

.mContainer {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 1s ease-in-out; /* Adjust timing and easing as needed */
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.bouncy-jump1 {
  animation: bounce 1s ease-in-out;
  animation-delay: 1s;
}

.bouncy-jump2 {
  animation: bounce 1s ease-in-out;
  animation-delay: 2s;
}

.bouncy-jump3 {
  animation: bounce 1s ease-in-out;
  animation-delay: 3s;
}

